<template>
	<view class="confirm-order-container">
		<!-- 确认订单 -->
		<view class="confirm-order-container-top">
			<view class="top-left">
				{{address == '' ? '请选择收货地址' : address}}
			</view>
			<image class="top-right" src="../../static/imgs/mine/right.png" mode="aspectFill"></image>
		</view>
		<view class="confirm-order-container-product">
			<view class="">
				<image :src="imgSrc" mode="aspectFill" class="product-img"></image>
			</view>
			<view class="product-container">
				<view class="name">
					{{name}}
				</view>
				<view class="price">
					<text style="font-size: 22rpx;">{{'HKD'}}<text
							style="font-size: 30rpx;">{{' ' + formatNumberRgx(price)}}</text></text>
					<text style="color: #2C405A;">x {{num}}</text>
				</view>
			</view>
		</view>
		<view class="confirm-order-container-bottom">
			<view class="delivery-type">
				<text>配送方式</text>
				<view class="type-right">
					<text>物流快递</text>
					<image class="right-img" src="../../static/imgs/classify/right.png" mode="aspectFill"></image>
				</view>
			</view>
			<view class="delivery-price">
				<text>商品金额</text>
				<text class="price-right" style="font-size: 22rpx;">{{'HKD'}}<text
						style="font-size: 30rpx;">{{' ' + formatNumberRgx(price)}}</text></text>
			</view>
		</view>
		<view class="confirm-order-container-total">
			<text class="total-num">共{{num}}件</text>
			<view class="total-price">
				<text>合计：</text>
				<text style="font-size: 22rpx;color: #ff8b62;">{{'HKD'}}<text
						style="font-size: 30rpx;">{{' ' + formatNumberRgx(price*num)}}</text></text>
			</view>
			<view class="total-button" @click="createOrder">提交订单</view>
		</view>
	</view>
</template>

<script>
	let RSA = require("../../utils/wxapp_rsa.js");
	export default {
		data() {
			return {
				address: '',
				buyId: null,
				imgSrc: null,
				name: "",
				price: "",
				num: null
			}
		},
		onLoad(option) {
			console.log(option);
			this.buyId = option.id;
			this.imgSrc = option.imgSrc;
			this.name = decodeURIComponent(option.name);
			this.price = option.price;
			this.num = option.num;
		},
		methods: {
			// 生成支付订单
			createOrder() {
				// 传递参数
				// 商品id、数量、商品价格、交易金额

				// 调用后端生成订单接口
				// 调用成功，跳转支付方式选择页面
				// #ifdef MP-WEIXIN
				uni.showLoading({
					title: "微信支付"
				});
				uni.request({
					url: this.$baseUrl + "/createOrder",
					method: "POST",
					data: {},
					header: {
						"content-type": "application/json",
						token: uni.getStorageSync('token')
					},
					complete: (res) => {
						uni.hideLoading();
						console.log('生成订单');
						console.log(res);
						let time = String(Date.now());
						let $chars = 'ABCDEFGHJKMNPQRSTWXYZabcdefhijkmnprstwxyz2345678';
						let maxPos = $chars.length;
						let str = '';
						for (let i = 0; i < 32; i++) {
							str += $chars.charAt(Math.floor(Math.random() * maxPos));
						}
						let paySign =
							"appId=wxd7cb2e8575630361\ntimeStamp=" + time + "\nnonceStr=" + str +
							"\npackage=prepay_id=" + res.data.prepay_id + '\n';

						let pubKey = 'a2582z25a17a1x0Ae70580c5edo3fe8M';

						let key = "-----BEGIN PRIVATE KEY-----" +
							"MIIEvAIBADANBgkqhkiG9w0BAQEFAASCBKYwggSiAgEAAoIBAQDSAfmcCgYjQcF0" +
							"/UqUszy4UMF9AgbPcXhy9WUk3esc+xYwMsTb5J/yNFZxJZG3AaYUy/GT5Iju4ynY" +
							"M5FMJtJC2is8cGtDEv1wdzpjpK3/9M8nlU3UA1vvAbXB9B4Fd4N03qUpg04t4dik" +
							"b5IRd0oUMO2dHoA8dAKTD5anz+jh78lt3xhWk+eo2ipbq+ewTrUh9aRMDK2x++TV" +
							"URYvp0X0fdJcgjAzGtvGnb/xRpU9l6cYCWy5Gbc5qTuc6reanB+YlsKmKc/FIRDN" +
							"7mNSvnAL6IfbgCb7XzCdPiQuXVvks9rjXqDALAuatsK4iMhsL9B3Xr4eWNlCZn1z" +
							"HBtTvRN7AgMBAAECggEAQn6rmVO3M8yrQ667wa+rBzVSvpE4tNBZ8hRvTSgTPN7n" +
							"3KUGnhWa62bVBAiARfD4tFcLC+ftz64P3swdMn6MTuyjigSSge9uXgDydQB8s7HB" +
							"Mehdd0cFAf2Jnnr8UoDQF94zb5yK0Y5UOWS2XF9oDlKbugchVYLqGcRy0HnNZFQk" +
							"nLdRvxIcyhgwDCh71tQeoXbhzE5KXafN9mupjbUG2Rl2PYUMdy3vRek3wkgHSGrO" +
							"Sr9IMHsL3DHIBEMSiT4OwLt+BmKzBxGS8FS/G3Nh3cqdqMmxWk3qaRtGFn/JWHfB" +
							"FGlSzZiBN0O6qgblFKVudG345Oz3MoZsRp9tEzp/gQKBgQDqYwXDeuN8PJTIS2g5" +
							"tmi5YAoTxRzcTh2NvSPWxk+AdgnoReVUZoaoLslxHXApZB1aEX8ldSkjNeSK+oN9" +
							"Rj1B+tH7O5iAxxNGhRxMndxbGKmTjYqfgWTCbSmWSPfxymPWPn/IySmGbuvC5Ea/" +
							"tzLOQ0cncwWKmJSHVC2Tpc5xQQKBgQDlX3SL/H6LOXsbncwi7+TORuqZW58QyhOQ" +
							"0LoVnCnCxz+HQkGwrerJonUiSSAwKKdoW2Kg+y/ftcefnxiL6J0w54CfE5v2YV6i" +
							"AZFEF0/jK1Kmin2/+pXp/8xR94hg9UbCQgX/cYjh0qxE9HmC1Y0QSzS5VjdDqk5U" +
							"qCUTE6EZuwKBgEukFVIWsJ+BdYqGO2aUpYtdoSLsM/otA4Cgj5ZafYG5lkztcash" +
							"Q0e3ga8NjwrskkRk7SGLg/EnDVCEtDaTmmVWqrlnbDnzBRd3EcDwGRMVDHTf8uKz" +
							"AW91hzxG5C2V4U5c8SQ9Nzd5SICm5uYsGytRrv7eMeWehCCAXA0QL2WBAoGAM8KP" +
							"pUtMLxTAkCHSwAuMgXQFWqgPRBWlxqcjZt7/mYGm91nfQwRsGCY98oe36MYzyNve" +
							"bN9iFUSLEJ0xR9ktJ60GHF+rcTTVyw85fbkZAPV0yDo2WE0u6UgdDl4ie2a+GfYW" +
							"LmercPI+h6z3gqu4EnY2JqCrBez3kikDfMOQNiMCgYAVyXfjqQWFPryaggpR+60b" +
							"pzvTul0Bgp8eKX4kvvn4WOo7i5QPw+sMFs35RME1QbWbUmirInRscKPdPqyCPjeC" +
							"6q6reAr8/RfmtKUA9P0IX+8ysOy0Fll8+aj+vyOQfVSgbsFROz7ktSOH2ASwnqfK" +
							"rz922JiOXkHQk+9MXIx31w==" + "-----END PRIVATE KEY-----";
						let encrypt_rsa = new RSA.RSAKey();
						encrypt_rsa = RSA.KEYUTIL.getKey(key);
						let encStr = encrypt_rsa.encrypt(paySign);
						encStr = RSA.hex2b64(encStr)

						console.log('签名')
						console.log(encStr);

						uni.requestPayment({
							// timeStamp: String(Date.now()),
							timeStamp: time,
							nonceStr: str,
							package: 'prepay_id=' + res.data.prepay_id,
							signType: 'RSA',
							paySign: encStr,
							success: (ret) => {
								console.log('success:');
								console.log(ret);
							},
							fail: (err) => {
								console.log('fail:');
								console.log(err);
							}
