					}
				})

				// #endif


				// uni.navigateTo({
				// 	url: "./payOrder"
				// })
			}
		},
	}
</script>

<style scoped lang="scss">
	.confirm-order-container {
		height: 100vh;
		background-color: #f9f9f9;

		.confirm-order-container-top {
			height: 100rpx;
			padding: 20rpx 20rpx;
			display: flex;
			justify-content: space-between;
			align-items: center;
			background-color: #fff;

			.top-left {
				color: #c3c3c3;
				font-size: 28rpx;
			}

			.top-right {
				width: 44rpx;
				height: 44rpx;
			}
		}

		.confirm-order-container-product {
			padding: 20rpx 40rpx;
			display: flex;

			.product-img {
				width: 200rpx;
				height: 200rpx;
				margin-right: 20rpx;
				border-radius: 30rpx;
			}

			.product-container {

				.name {
					font-size: 28rpx;
				}

				.price {
					margin-top: 10rpx;
					color: #ff8b62;
					display: flex;
					justify-content: space-between;
				}
			}
		}

		.confirm-order-container-bottom {
			padding: 0 20rpx;

			.delivery-type {
				height: 80rpx;
				display: flex;
				justify-content: space-between;
				align-items: center;
				font-size: 28rpx;

				.type-right {
					display: flex;
					align-items: center;

					image {
						width: 32rpx;
						height: 32rpx;
					}
				}
			}

			.delivery-price {
				height: 80rpx;
				display: flex;
				justify-content: space-between;
				align-items: center;
				font-size: 28rpx;

				.price-right {
					color: #ff8b62;
				}
			}
		}

		.confirm-order-container-total {
			height: 120rpx;
			display: flex;
			justify-content: flex-end;
			align-items: center;
			position: absolute;
			bottom: 0;
			left: 0;
			right: 0;
			background-color: #fff;

			.total-num {
				color: #c3c3c3;
				font-size: 24rpx;
			}

			.total-price {
				margin-left: 20rpx;
				font-size: 30rpx;
			}

			.total-button {
				margin: 0 20rpx 0 60rpx;
				padding: 20rpx 40rpx;
				background-color: #85dbd0;
				color: #fff;
				font-size: 26rpx;
				border-radius: 60rpx;
			}
		}
	}
</style>
